.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 0%;
}

.about__me-image:hover {
    transform: rotate(0)
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__title {
    text-align: left;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 1rem
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-black);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-black);
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: left;
}

/* Scroll down */

.scroll__down {
    position: absolute;
    right: 20rem;
    bottom: 10rem;
}

/* Media Queries (Medium Devices like tablets) */

@media screen and (max-width: 1600px) {
    .scroll__down {
        right: 14rem;
        bottom: 7rem;
    }
}

@media screen and (max-width: 1300px) {
    .scroll__down {
        display: none;
    }
}

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }

    .scroll__down {
        display: none;
    }
}

/* Media Queries (Small Devices like phones) */

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
}